    </BackItem>
    <BackItem model="bg" :type="1">
      <div class="myNum">
        <div class="header">
          <p class="label">{{ $t('lottery.我的号码') }}</p>
          <img class="refresh" :src="refreshIcon" @click="getDrawInfo">
        </div>
        <div class="content" v-if="info.myLoft.length !== 0">
