<script>

import BackItem from '../components/BackItem.vue'
import CurrentBox from '../components/CurrentBox.vue'
import { Toast } from 'vant'
import helper from '../utils/helper'
import Header from '../components/Head.vue'
// import dayjs from 'dayjs'
export default {
  name: 'draw',
  components: {
    BackItem,
    CurrentBox,
    Header
  },
  data () {
    return {
      refreshIcon: require('../assets/refresh.png'),
      showBox: false,
      // 四种打开弹窗的情况 1、开奖记录 2、我的号码 3、授权 4、投注
      flag: 1,
      // 开奖记录和我的号码列表
      numbersList: [],
      // 授权数量
      // amount: '',
      // 投注号码表
      betNumbers: [],
      // 抽奖信息
      info: {
        record: [],
        myLoft: []
      },
      // 地址
      address: '',
      // 投注的倍数
      times: 1,
      // 是否授权
      isAuthorize: false,
      // etf余额
      balance_etf: 0,
      startTime: 0,
      timer: null
    }
  },
  watch: {
    startTime(newValue) {
      if (newValue === 0) {
        clearInterval(this.timer)
      }
    },
    address(newValue) {
      if (newValue) {
        this.getDrawInfo()
      }
    }
  },
  computed: {
    d () {
      const d = parseInt(this.startTime / 60 / 60 / 24, 10)
      return d
    },
    h () {
      const h = parseInt(this.startTime / 60 / 60 % 24, 10)
      return h
    },
    m () {
      const h = parseInt(this.startTime / 60 % 60, 10)
      return h
    },
    s () {
      const s = parseInt(this.startTime % 60, 10)
      return s
    },
    boxTitle () {
      // 弹窗标题
      switch (this.flag) {
        case 1:
          return this.$t('lottery.开奖记录')
        case 2:
          return this.$t('lottery.我的号码')
        case 3:
          return `${this.$t('lottery.授权')} ETF`
        case 4:
          return this.$t('lottery.参与投注')
        default:
          return this.$t('lottery.开奖记录')
      }
    }
  },
  methods: {
    // 查看更多
    handleMore(flag) {
      if (flag === 1) {
        // 开奖记录
        this.flag = 1
        this.showBox = true
        // 获取开奖记录
        this.numbersList = this.info.record
      } else {
        // 我的号码
        this.flag = 2
        this.showBox = true
        // 获取号码
        this.numbersList = this.info.myLoft
      }
    },
    // 追加授权(授权)
    async handleAdd() {
      // this.flag = 3
      // this.showBox = true
      // 直接授权不弹窗
      try {
        const result = await helper.approve()
        // 获取数据
        if (result) {
          Toast.success(this.$t('lottery.授权成功'))
          this.getDrawInfo()
        }
      } catch {
        Toast.fail(this.$t('lottery.授权失败'))
      }
    },
    // 选择投注号码
    selectNumber(val) {
      // 判断是否已经选择当前值
      if (this.betNumbers.includes(val)) {
        // 去除
        this.betNumbers.splice(this.betNumbers.indexOf(val), 1)
      } else {
        // 最多选中5个号码
        if (this.betNumbers.length === 5) {
          Toast.fail(this.$t('lottery.最多选择5个号码'))
        } else {
          this.betNumbers.push(val)
        }
      }
    },
    // 投注
    async handleBet() {
      // 授权数量为0无法投注
      this.flag = 4
      this.showBox = true
      try {
        const notZero = await helper.hasApprove()
        if (!notZero) {
          Toast.fail(this.$t('lottery.无法进行投注'))
        } else {
          this.flag = 4
          // this.showBox = true
        }
      } catch {
        Toast.fail(this.$t('lottery.无法进行投注'))
      }
    },
    // 取消
    handleCancel() {
      // 关闭弹窗，重置数据
      if (this.flag === 1 || this.flag === 2) {
        this.numbersList = []
      } else if (this.flag === 4) {
        this.betNumbers = []
        this.times = 1
      }
      this.showBox = false
    },
    // 确认
    async handleConfirm() {
      if (this.flag === 4) {
        // 投注
        // 获取ETH余额，判断是否可以投注
        const { data } = await this.$api.getMy(this.address)
        if (data.status === 1) {
          this.balance_etf = data.data.balance_etf
        } else {
          Toast.fail(data.msg)
        }
        if (this.balance_etf < this.times * 2) {
          Toast.fail(`${this.$t('lottery.本次投注需要')}${this.times * 2}ETF，${this.$t('lottery.余额不足')}`)
          return
        }
        try {
          const result = await helper.buyLoft(this.betNumbers, this.times)
          if (result) {
            Toast.success(this.$t('lottery.投注成功'))
            this.handleCancel()
            this.getDrawInfo()
          }
        } catch {
          Toast.fail(this.$t('lottery.投注成功'))
        }
      }
    },
    // 获取抽奖信息
    async getDrawInfo() {
      const { data } = await this.$api.getDraw(this.address)
      if (data.status === 1) {
        this.info = data.data
        const timeStamp = Date.parse(new Date())
        this.startTime = this.info.next_run - timeStamp / 1000
        if (this.startTime > 0) {
          if (this.timer) {
            clearInterval(this.timer)
          }
          this.timer = setInterval(() => {
            this.startTime--
          }, 1000)
        }
      } else {
        Toast.fail(data.msg)
      }
      this.isAuthorize = await helper.hasApprove()
    },
    // 提取
    async handleExtract(type) {
      const flag = type === 'ETH' ? 'loftETH' : 'loftETF'
      // 获取amount hash sign
      try {
        const { data } = await this.$api.claimInfo({
          address: this.address,
          contract: flag
        })
        if (data.status === 1) {
          let result = null
          // 执行提取ETF、ETH
          if (type === 'ETH') {
            result = await helper.ETHCollect({ ...data.data })
          } else if (type === 'ETF') {
            result = await helper.ETFCollect({ ...data.data })
          }
          if (result) {
            Toast.success(this.$t('lottery.提取成功'))
            this.getDrawInfo()
          } else {
            Toast.fail(this.$t('lottery.提取失败'))
          }
        } else {
          Toast.fail(data.msg)
        }
      } catch {
        Toast.fail(this.$t('lottery.提取失败'))
      }
    }
  },
  created () {
    this.address = window.address
    // 总线注入
    this.bus.$on('addressChange', (address) => {
      this.address = address
    })
  },
  destroyed() {
    clearInterval(this.timer)
  }

}
</script>
