import axios from 'axios'
import baseUrl from '../config/baseUrl'
var instance = axios.create()
// 实例配置
instance.defaults.timeout = 50000
// instance.defaults.baseURL = 'http://mock.belew.tech/mock/426'
instance.defaults.baseURL = baseUrl
instance.defaults.withCredentials = true
// 实例拦截器
instance.interceptors.request.use(
  config => {
    return config
  },
  error => {
    return Promise.reject(error)
  }
)
instance.interceptors.response.use(
  response => {
    return response
  },
  error => {
    return Promise.reject(error)
  }
)

// config 将会与实例的config合并
var axiosMethod = function(method, url, params, config = {}) {
  return new Promise((resolve, reject) => {
    // 方法get delete 中的params 对应的是config
    instance[method](url, params, config)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

const http = {
  get(url, config) {
    return axiosMethod('get', url, config)
  },
  delete(url, config) {
    return axiosMethod('delete', url, config)
  },
  post(url, data, config) {
    return axiosMethod('post', url, data, config)
  },
  put(url, data, config) {
    return axiosMethod('put', url, data, config)
  }
}
export default http
