import Web3 from 'web3'
// 代币合约
import coinAbi from './abi/coin.json'
// 分红合约
import shareAbi from './abi/share.json'
// 激励合约
import impelAbi from './abi/impel.json'
// 保险合约
import insuranceAbi from './abi/insurance.json'
// 抽奖合约
import drawAbi from './abi/draw.json'
// import BigNumber from 'bignumber.js'
// 测试地址
// const coinAddress = '0x5ab1D5184263BFaaA00ce7eA9b09a12E4B33912D'
// const shareAddress = '0x13cE85e9F471aA011af6E4Da8F83C57411646b87'
// const impelAddress = '0x9da78BCb3Ff5e6c3E36b3Fd7152A3fbfF80F66fb'
// const insuranceAddress = '0xC579693F34abf7544A84acA7a9547C67f01C77f2'
// const drawAddress = '0xdC0af99DCab5148Df323eEA9fB0284040A8c2295'
// 正式地址
const coinAddress = '0x9E101C3a19e38a02B3c2fCf0D2Be4CE62C846488'
const shareAddress = '0x72E662Fb393DCB9c6d96B6d63D13Eb84370fd583'
const impelAddress = '0x350BDC46d931712d83ef989725Ba4904C487F360'
const insuranceAddress = '0xF769E8993f10bE2fc0032863f2C63336Cc21478C'
const drawAddress = '0xa482246fFFBf92659A22525820C665D4aFfCF97B'
const helper = {
  // 判断是否有web3对象
  isProvider: async () => {
    if (window && window.web3 === undefined) {
      return false
    } else {
      window.web3 = new Web3(window.web3.currentProvider)
      return true
    }
  },
  // 登录metaMask
  enableMetaMask: async () => {
    if (window.ethereum) {
      try {
        await window.ethereum.enable()
        return { success: true }
      } catch (error) {
        return { success: false, msg: error }
      }
    } else {
      return { success: false, msg: '不存在window.etheruem' }
    }
  },
  // 初始化合约
  initContract() {
    if (window.web3) {
      window.coinContract = new window.web3.eth.Contract(coinAbi, coinAddress)
      window.shareContract = new window.web3.eth.Contract(shareAbi, shareAddress)
      window.impelContract = new window.web3.eth.Contract(impelAbi, impelAddress)
      window.insuranceContract = new window.web3.eth.Contract(insuranceAbi, insuranceAddress)
      window.drawContract = new window.web3.eth.Contract(drawAbi, drawAddress)
    }
  },
  // 授权
  approve: async(value = window.web3.utils.toTwosComplement('-1')) => {
    const result = await window.coinContract.methods.approve(drawAddress, value).send({
      from: window.address
    })
    console.log(result)
    return result
  },
  // 是否授权
  hasApprove: async() => {
    const num = await window.coinContract.methods.allowance(window.address, drawAddress).call()
    return Number(num) !== 0
  },
  // 获取metaMask地址
  getAccounts: async() => {
    const result = await window.web3.eth.getAccounts()
    return result
  },
  // 排行的提取
  impelCollect: async(amount, hash, sign) => {
    const result = await window.impelContract.methods.claim(amount, hash, sign).send({
      from: window.address
    })
    return result
  },
  // 抽奖的提取ETF、ETH、投注
  ETHCollect: async(amount, hash, sign) => {
    const result = await window.drawContract.methods.claimETH(amount, hash, sign).send({
      from: window.address
    })
    return result
  },
  ETFCollect: async(amount, hash, sign) => {
    const result = await window.drawContract.methods.claimETF(amount, hash, sign).send({
      from: window.address
    })
    return result
  },
  // 投注
  buyLoft: async(nums, times) => {
    const result = await window.drawContract.methods.buyLoft(nums, times).send({
      from: window.address
    })
    return result
  },
  // 我的页面 领取赔付、提取收益、参与投资
  // 领取赔付
  reported: async(amount, hash, sign) => {
    const result = await window.insuranceContract.methods.claim(amount, hash, sign).send({
      from: window.address
    })
    return result
  },
  // 提取收益
  myCollect: async(amount, hash, sign) => {
    const result = await window.shareContract.methods.claim(amount, hash, sign).send({
      from: window.address
    })
    return result
  },
  // 参与投资
  invest: async(value) => {
    const valueBN = `${value}`
    debugger
    const result = await window.shareContract.methods.invest().send({
      from: window.address,
      value: window.web3.utils.toWei(valueBN, 'ether')
      // gas: 3000000
    })
    return result
  }
}
export default helper
