<template>
  <div id="app">
    <!-- 语言 -->
    <Language v-if="active === 0"></Language>
    <warm v-model="warm_status"></warm>
    <register v-model="reg_status" :address="address" :code="code"></register>
    <router-view/>
    <!-- 底部tabbar -->
    <van-tabbar v-model="active" z-index="2" v-show="show" active-color="#F6BD4D" inactive-color="#666666" :border="false" :safe-area-inset-bottom="true" @change="onChange" style="background-color: #060304;">
      <van-tabbar-item>
        <span>{{ tabText.home }}</span>
        <template #icon="props">
          <img :src="props.active ? home.active : home.inactive" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item>
        <span>{{ tabText.draw }}</span>
        <template #icon="props">
          <img :src="props.active ? draw.active : draw.inactive" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item>
        <span>{{ tabText.rank }}</span>
        <template #icon="props">
          <img :src="props.active ? rank.active : rank.inactive" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item>
        <span>{{ tabText.my }}</span>
        <template #icon="props">
          <img :src="props.active ? my.active : my.inactive" />
        </template>
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<script>
import helper from './utils/helper'
import WarmComponent from './components/Warm.vue'
import registerComponent from './components/Register.vue'
import Language from './components/Language.vue'
export default {
  components: {
    warm: WarmComponent,
    register: registerComponent,
    Language
  },
  provide() {
    return {
    }
  },
  data() {
    return {
      warm_status: false,
      reg_status: false,
      address: '',
      // 底部导航栏
      active: 0,
      show: true,
      code: '',
      home: {
        active: require('./assets/tabbar/homeA.png'),
        inactive: require('./assets/tabbar/homeB.png')
      },
      draw: {
        active: require('./assets/tabbar/drawA.png'),
        inactive: require('./assets/tabbar/drawB.png')
      },
      rank: {
        active: require('./assets/tabbar/rankA.png'),
        inactive: require('./assets/tabbar/rankB.png')
      },
      my: {
        active: require('./assets/tabbar/myA.png'),
        inactive: require('./assets/tabbar/myB.png')
      }
    }
  },
  computed: {
    tabText () {
      return {
        home: this.$t('tabbar.总览'),
        draw: this.$t('tabbar.抽奖'),
        rank: this.$t('tabbar.排行'),
        my: this.$t('tabbar.我的')
      }
    }
  },
  watch: {
    address(newValue) {
      this.bus.$emit('addressChange', newValue)
      window.address = newValue
    },
    // 监视路由
    $route: {
      handler(to, from) {
        this.showTabbar(to)
        if (this.$route.name === 'home') {
          this.active = 0
        } else if (this.$route.name === 'draw') {
          this.active = 1
        } else if (this.$route.name === 'rank') {
          this.active = 2
        } else if (this.$route.name === 'my') {
          this.active = 3
        }
      },
      immediate: true
    }
  },
  methods: {
    // 当前地址是否注册过，没有注册则唤起注册页面
    async checkedAddress() {
      const isRegister = await this.$api.isCheckAddress(this.address)
      if (!isRegister.data.data.reg) {
        this.reg_status = !isRegister.data.data.reg
      }
    },
    // 登入成功赋值地址设置监听
    async actionAddress() {
      const accounts = await helper.getAccounts()
      this.address = accounts[0]
      await this.checkedAddress()
      window.ethereum.on('accountsChanged', async (accounts) => {
        this.address = accounts[0]
        await this.checkedAddress()
      })
    },
    async enablMetaMask() {
      // 判断当前账号是否已经拥有地址,没有地址则登入
      const accounts = await helper.getAccounts()
      if (accounts.length <= 0) {
        const isLogin = await helper.enableMetaMask()
        // 登入成功
        if (isLogin.success) {
          this.actionAddress()
        }
      } else {
      // 登入成功
        this.actionAddress()
      }
    },
    // 显示底部tabbar页
    showTabbar (data) {
      const List = ['home', 'draw', 'rank', 'my']
      if (List.indexOf(data.name) > -1) {
        this.show = true
      } else {
        this.show = false
      }
    },
    // 底部tabbar切换
    onChange (index) {
      if (index === 0) {
        this.$router.push({
          name: 'home'
        })
      } else if (index === 1) {
        this.$router.push({
          name: 'draw'
        })
      } else if (index === 2) {
        this.$router.push({
          name: 'rank'
        })
      } else if (index === 3) {
        this.$router.push({
          name: 'my'
        })
      }
    }
  },
  async created() {
    if (this.$route.query.code) {
      this.code = this.$route.query.code
    }
    const result = await helper.isProvider()
    if (!result) {
      this.warm_status = true
      return
    } else {
      this.warm_status = false
      helper.initContract()
    }
    this.enablMetaMask()
  }
}
</script>
