import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import api from './request/index'
import 'vant/lib/index.css'
import Vant from 'vant'
import Bignumber from 'bignumber.js'
import dayjs from 'dayjs'
import 'xe-utils'
import VXETable from 'vxe-table'
import 'vxe-table/lib/style.css'
import Vuei8n from 'vue-i18n'
import i18n from './i18n/index'
Vue.use(Vant)
Vue.use(VXETable)
Vue.use(Vuei8n)

Vue.filter('bigNumberFilter', function (value, index) {
  return new Bignumber(value).dp(index, 1).toFixed()
})
Vue.filter('hashFilter', function (value) {
  return `${value.substring(0, 8)}.....${value.substring(value.length - 9)}`
})
Vue.filter('timeFilter', function (value) {
  return dayjs(value * 1000).format('YYYY-MM-DD HH:mm:ss')
})
Vue.config.productionTip = false
Vue.prototype.$api = api
// 中央事物总线
Vue.prototype.bus = new Vue()
new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
