<template>
  <div class="languageWrapper">
    <van-dropdown-menu active-color="#F6AF4D" :overlay="false">
      <van-dropdown-item v-model="value" :options="language" />
    </van-dropdown-menu>
  </div>
</template>

<script>
export default {
  data () {
    return {
      value: 0,
      lang: ''
    }
  },
  created () {
    // 默认中文
    if (localStorage.getItem('language')) {
      this.lang = localStorage.getItem('language')
    } else {
      localStorage.setItem('language', 'zh')
    }
  },
  computed: {
    language () {
      return [
        { text: 'CN', value: 0 },
        { text: 'EN', value: 1 },
        { text: 'JP', value: 2 },
        { text: 'KR', value: 3 }
      ]
    }
  },
  watch: {
    value (val) {
      if (val === 0) {
        // 设置中文
        localStorage.setItem('language', 'zh')
        this.$i18n.locale = 'zh'
      } else if (val === 1) {
        // 设置英文
        localStorage.setItem('language', 'gb')
        this.$i18n.locale = 'gb'
      } else if (val === 2) {
        // 设置日文
        localStorage.setItem('language', 'jp')
        this.$i18n.locale = 'jp'
      } else if (val === 3) {
        // 设置韩文
        localStorage.setItem('language', 'kr')
        this.$i18n.locale = 'kr'
      }
    },
    lang (val) {
      if (val === 'zh') {
        this.value = 0
      } else if (val === 'gb') {
        this.value = 1
      } else if (val === 'jp') {
        this.value = 2
      } else if (val === 'kr') {
        this.value = 3
      }
    }
  }
}
</script>

