<template>
  <div class="backWrapper flex_col_center" :style="`background: url(${backImage}) no-repeat;background-size: 100% 100%;`">
    <slot></slot>
  </div>
</template>

<script>

export default {
  props: {
    model: {
      type: String,
      required: true,
      default: 'bg'
    },
    type: {
      type: Number,
      required: true,
      default: 1
    }
  },
  data () {
    return {
      bg1: require('../assets/bg1.png'),
      bg2: require('../assets/bg2.png'),
      bg3: require('../assets/bg3.png'),
      btn1: require('../assets/btn1.png'),
      btn2: require('../assets/btn2.png')
    }
  },
  computed: {
    backImage (newVal, oldVal) {
      if (this.model === 'bg') {
        return this[`bg${this.type}`]
      } else if (this.model === 'btn') {
        return this[`btn${this.type}`]
      } else {
        return this[`${this.model}${this.type}`]
      }
    }
  }
}
</script>
<style lang="less" scoped>
.backWrapper{
  background-size: 100% 100%;
}
</style>
