<template>
    <div class="register-box fade" v-if="inner_status">
      <!-- 语言 -->
      <Language></Language>
      <div class="item flex_col_center mg-t-30" style="margin-top: 0.8rem;">
        <p class="fs-w fs-18 color-white">{{$t('当前地址')}}</p>
        <!-- <input type="text" class="inner_input" v-model="address"> -->
        <div class="inner_input">{{address}}</div>
      </div>
      <div class="item flex_col_center mg-t-30">
        <p class="fs-w fs-18 color-white">{{$t('邀请码')}}</p>
        <input type="text" class="inner_input" v-model="inviter_code">
      </div>
      <div class="item flex_col_center mg-t-30">
        <p class="fs-w fs-18 color-white">{{$t('接点人')}}</p>
        <input type="text" v-model="team_code" class="inner_input">
      </div>
      <BackItem style="width:100%;height:1rem" model="btn" :type="1" @click.native="handleBind">
        <p class="fs-18">{{$t('绑定地址')}}</p>
      </BackItem>
    </div>
</template>
<script>
import BackItem from './BackItem'
import Language from '../components/Language'
import { Toast } from 'vant'
import Vue from 'vue'
Vue.use(Toast)
export default {
  components: {
    BackItem,
    Language
  },
  props: {
    value: {
      type: Boolean,
      default: true
    },
    address: {
      type: String,
      default: ''
    },
    code: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      inner_status: this.value,
      inviter_code: this.code,
      team_code: ''
    }
  },
  watch: {
    value: {
      handler(newValue) {
        this.inner_status = newValue
      }
    }
  },
  methods: {
    handleBind() {
      this.registerAddress()
    },
    async registerAddress() {
      const { data } = await this.$api.register({ address: this.address, inviter_code: this.inviter_code, team_code: this.team_code })
      if (data.status === 1) {
        Toast.success('绑定成功')
        this.$emit('input', false)
      } else {
        Toast.fail(data.msg)
      }
    }
  },
  created() { }
}
</script>
