.fade {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0px;
  top: 0px;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  background:rgba(0, 0, 0, 0.8);
}
.register-box{
  padding: .1rem 4%;
  box-sizing: border-box;
  .item{
    width: 100%;
    p{
      width: 100%;
      text-align: left;
    }
  }
}
</style>
