<template>
  <div class="rank">
    <!-- <div class="header">
      <p class="label">{{ $t('rank.激励池') }}</p>
      <p class="amount">{{ info.top_balance }} ETH</p>
    </div> -->
    <Header :title="$t('rank.激励池')" :number="info.top_balance"></Header>
    <BackItem model="bg" :type="2" style="margin-top: 0.245rem;">
      <div class="contract">
        <div class="address">
          <p class="label">{{ $t('rank.合约地址') }}</p>
          <p class="value">{{ info.top_contract }}</p>
        </div>
        <div class="time">
          <div class="price" v-if="startTime > 0">
            <p class="label">{{ $t('倒计时') }}</p>
            <p class="value mg-l-10" style="width:.5rem;overflow:unset">{{d}}{{$t('日')}}{{h}}{{$t('时')}}{{m}}{{$t('分')}}{{s}}{{$t('秒')}}</p>
          </div>
          <div class="price" v-else>
            <p class="label">{{ $t('已开奖') }}</p>
          </div>
          <div class="refresh">
            <img class="icon" :src="refreshIcon" alt="" @click="getRankInfo">
          </div>
        </div>
      </div>
    </BackItem>
    <BackItem model="bg" :type="1">
      <div class="records">
        <div class="content">
          <div class="label">
            <p class="period">{{ $t('rank.排名') }}</p>
            <p class="block">{{ $t('rank.用户地址') }}</p>
            <p class="number">{{ $t('rank.直推') }}ETH</p>
            <p class="number">{{ $t('rank.预计奖励') }}</p>
          </div>
          <div class="main" v-if="info.record.length > 0">
            <div class="item" v-for="(item, index) in info.record" :key="index">
              <p class="period">{{ item.index }}</p>
              <p class="block">{{ item.address }}</p>
              <p class="number">{{ item.amount }}</p>
              <p class="number">{{ item.est_reward }}</p>
            </div>
          </div>
           <div class="content" v-else style="font-size: 0.15rem; line-height: 2rem;">{{ $t('lottery.暂无记录') }}</div>
        </div>
      </div>
    </BackItem>
    <div class="tip">
      <p class="message">{{ $t('rank.排行规则') }}</p>
    </div>
    <BackItem model="bg" :type="3">
      <div class="rewards">
        <img class="refresh" :src="refreshIcon" alt="" @click="getRankInfo">
        <div class="item eth">
          <p class="label">{{ $t('rank.本周的直推累计') }}</p>
          <p class="amount">{{ info.myTOP }} ETH</p>
          <p class="extract">
            <BackItem model="btn" :type="1">提取</BackItem>
          </p>
        </div>
        <div class="item">
          <p class="label">{{ $t('rank.待提取排行奖励') }}</p>
          <p class="amount">{{ info.pendingETH }} ETH</p>
          <p class="extract">
            <BackItem model="btn" :type="1" @click.native="handleExtract">{{ $t('rank.提取') }}</BackItem>
          </p>
        </div>
      </div>
    </BackItem>
  </div>
</template>

<script>
import BackItem from '../components/BackItem.vue'
import { Toast } from 'vant'
import helper from '../utils/helper'
import dayjs from 'dayjs'
import Header from '../components/Head.vue'
export default {
  name: 'rank',
  components: {
    BackItem,
    Header
  },
  data () {
    return {
      refreshIcon: require('../assets/refresh.png'),
      // 排行信息
      info: {},
      // 地址
      address: '',
      startTime: 0,
      timer: null
    }
  },
  watch: {
    startTime(newValue) {
      if (newValue === 0) {
        clearInterval(this.timer)
      }
    },
    address(newValue) {
      if (newValue) {
        this.getRankInfo()
      }
    }
  },
  filters: {
    daysFilter(val) {
      const second = dayjs(val * 1000).diff(dayjs(), 'second')
      const days = Math.floor(second / 86400)
      return days
    },
    hoursFilter(val) {
      const second = dayjs(val * 1000).diff(dayjs(), 'second')
      const hours = Math.floor((second % 86400) / 3600)
      return hours
    }
  },
  computed: {
    d () {
      const d = parseInt(this.startTime / 60 / 60 / 24, 10)
      return d
    },
    h () {
      const h = parseInt(this.startTime / 60 / 60 % 24, 10)
      return h
    },
    m () {
      const h = parseInt(this.startTime / 60 % 60, 10)
      return h
    },
    s () {
      const s = parseInt(this.startTime % 60, 10)
      return s
    }
  },
  methods: {
    async getRankInfo () {
      const { data } = await this.$api.getInfoTop(this.address)
      if (data.status === 1) {
        this.info = data.data
        const timeStamp = Date.parse(new Date())
        this.startTime = this.info.next_run - timeStamp / 1000
        if (this.startTime > 0) {
          if (this.timer) {
            clearInterval(this.timer)
          }
          this.timer = setInterval(() => {
            this.startTime--
          }, 1000)
        }
      } else {
        Toast.fail(data.msg)
      }
    },
    // 提取
    async handleExtract () {
      try {
        // 获取amount hash sign
        const { data } = await this.$api.claimInfo({
          address: this.address,
          contract: 'top'
        })
        if (data.status === 1) {
          // 提取操作
          const result = await helper.impelCollect({ ...data.data })
          if (result) {
            Toast.success(this.$t('rank.提取成功'))
            this.getRankInfo()
          } else {
            Toast.fail(this.$t('rank.提取失败'))
          }
        } else {
          Toast.fail(data.msg)
        }
      } catch {
        Toast.fail(this.$t('rank.提取失败'))
      }
    }
  },
  created () {
    this.address = window.address
    // 总线注入
    this.bus.$on('addressChange', (address) => {
      this.address = address
    })
  }
}
</script>
