<script>
import { Toast } from 'vant'
import BackItem from '../components/BackItem'
import CurrentBox from '../components/CurrentBox.vue'
import helper from '../utils/helper'
import BigNumber from 'bignumber.js'
import baseUrl from '../config/baseUrl'
import VeTree from 'v-charts/lib/tree.common'
// import { Tab, Tabs } from 'vant'
const treeData = {
  name: 'f',
  value: 1,
  // 树节点的最顶层，不让重新点击
  limit: '',
  children: [
    {
      name: 'a-a',
      value: 2
    },
    {
      name: 'a-b',
      value: 2
    }
  ]
}
export default {
  components: {
    BackItem,
    CurrentBox,
    VeTree
  },
  name: 'my',
  data() {
    var self = this
    this.chartEvents = {
      click: function (e) {
        if (e.data.address === '空缺') {
          Toast.fail('暂无地址')
          return
        }
        self.handleTree(e.data)
      }
    }
    return {
      that: this,
      chartSettings: {
        seriesMap: {
          树状图: {
            orient: 'vertical',
            label: {
              position: 'top',
              fontWeight: '900'
            },
            symbolSize: 20
          }
        }
      },
      chartData: {
        columns: ['name', 'value'],
        rows: [
          {
            name: '树状图',
            value: [treeData]
          }
        ]
      },
      address: '',
      refresh: require('../assets/refresh.png'),
      paidStatus: false,
      outStatus: false,
      profitStats: false,
      teamStatus: false,
      investStatus: false,
      // 是否赔付
      isCover: false,
      active: 0,
      isout: true,
      info: {},
      coverHistory: [],
      outTable: [],
      directTable: [],
      generationTable: [],
      investNumber: '0.1',
      max_invest: 0,
      min_invest: 0,
      head: {},
      // 点位表格
      pointsTable: []
    }
  },
  watch: {
    address(newValue) {
      if (newValue) {
        this.getInfo()
      }
    }
  },
  methods: {
    formatterNumber({ cellValue }) {
      return new BigNumber(cellValue).dp(2, 1).toFixed()
    },
    tabChange(value) {
      if (value === 2) {
        this.$nextTick(_ => {
          this.$refs['vetree'].echarts.resize()
        })
      }
    },
    handleInvite() {
      try {
        const url = `${baseUrl}?code=${this.info.invite_code}`
        var input = document.createElement('input')
        input.setAttribute('readonly', 'readonly') // 防止手机上弹出软键盘
        input.setAttribute('value', url)
        document.body.appendChild(input)
        input.select()
        document.execCommand('copy')
        document.body.removeChild(input)
        Toast.success(this.$t('复制成功'))
      } catch (error) {
        Toast.success(this.$t('复制失败'))
      }
    },
    setTree(dataTree) {
      const relative = dataTree
      let data = {}
      data = {
        name: relative.current_code || '空缺',
        id: relative.current_code || '空缺',
        address: relative.current || '空缺',
        superAddress: relative.Father,
        children: [
          {
            name: relative.A_code || '空缺',
            id: relative.A_code || '空缺',
            address: relative.A || '空缺'
          },
          {
            name: relative.B_code || '空缺',
            id: relative.B_code || '空缺',
            address: relative.B || '空缺'
          }
        ]
      }
      this.chartData.rows[0].value = [data]
      // this.pointsTable.push(relative)
      this.$nextTick(_ => {
        this.$refs['vetree'].echarts.resize()
      })
    },
    async handleTree(data) {
      console.log(data)
      let address = data.address
      if (this.limit === address) {
        return
      }
      // 除了初始的点其他的时候点击自身等于点击上级
      if (data.superAddress) {
        address = data.superAddress
      }
      const tree = await this.$api.getInviteTree({ address })
      if (tree.data.status === 1) {
        const relative = tree.data.data
        this.setTree(relative)
      } else {
        Toast.fail(tree.data.msg)
      }
    },
    async handlePaid() {
      const { data } = await this.$api.getCoverHistory(this.address)
      if (data.status === 1) {
        this.coverHistory = data.data
        this.paidStatus = true
      } else {
        Toast.fail(data.msg)
      }
    },
    async handlePaider() {
      try {
        const { data } = await this.$api.claimInfo({ address: this.address, contract: 'cover' })
        if (data.status === 1) {
          const params = data.data
          console.log(params)
          const reuslt = await helper.reported(params.amount, params.hash, params.sign)
          if (reuslt) {
            Toast.success(this.$t('领取成功'))
          }
        } else {
          Toast.fail(data.msg)
        }
      } catch (error) {
        console.log(this.$t('领取赔付失败') + error)
        Toast.fail(this.$t('领取赔付失败'))
      }
    },
    async handleCollect() {
      try {
        const { data } = await this.$api.claimInfo({ address: this.address, contract: 'main' })
        if (data.status === 1) {
          const params = data.data
          const reuslt = await helper.myCollect(params.amount, params.hash, params.sign)
          if (reuslt) {
            Toast.success(this.$t('提取成功'))
          }
        } else {
          Toast.fail(data.msg)
        }
      } catch (error) {
        console.log(this.$t('提取失败') + error)
        Toast.fail(this.$t('提取失败'))
      }
    },
    async handleOut() {
      const { data } = await this.$api.getOutHistory(this.address)
      if (data.status === 1) {
        this.outTable = data.data
        this.outStatus = true
      } else {
        Toast.fail(data.msg)
      }
    },
    handleProfit() {
      this.profitStats = true
    },
    // 追加复投，参与投资
    handleAction() {
      this.handleInvest()
    },
    async handleInvest() {
      try {
        const a = Number(this.investNumber)
        // 参与投资是个整数
        if (!(a >= this.min_invest && a <= this.max_invest)) {
          Toast.fail(this.$t('请在规定范围内投资'))
          return
        }
        if (a !== this.min_invest && new BigNumber(this.investNumber).mod(0.1).toNumber() !== 0) {
          Toast.fail(this.$t('需要0.1的整数倍'))
          return
        }
        console.log(this.investNumber)
        this.investStatus = false
        const result = await helper.invest(this.investNumber)
        console.log(result)
        if (result) {
          Toast.success(this.$t('投资成功'))
          await this.getInfo()
        }
      } catch (error) {
        console.log(this.$t('投资失败') + error)
        Toast.fail(this.$t('投资失败'))
      }
    },
    async handleTeamer() {
      try {
        const direct = await this.$api.getInvite({ address: this.address, limit: 10000000, offset: 0 })
        const head = await this.$api.getInviteInfo({ address: this.address })
        const tree = await this.$api.getInviteTree({ address: this.address })
        // 获取树顶层显示，第一次请求树写入
        this.limit = tree.data.data.current
        const generation = await this.$api.getInviteInfoGeneration(this.address)
        if (direct.data.status === 1 && head.data.status === 1 && tree.data.status === 1 && generation.data.status === 1) {
          this.directTable = direct.data.data
          this.head = head.data.data
          this.generationTable = generation.data.data
          // const relative = tree.data.data
          // this.pointsTable = []
          // if (!relative.A) {
          //   relative.A = this.$t('暂无')
          // }
          // if (!relative.B) {
          //   relative.B = this.$t('暂无')
          // }
          // this.pointsTable.push(relative)
          const relative = tree.data.data
          this.setTree(relative)
          this.teamStatus = true
        } else {
          Toast.fail(this.$t('邀请信息失败'))
        }
      } catch (error) {
        console.log(error)
        Toast.fail(this.$t('下级信息获取失败'))
      }
    },
    // 我的页面基本信息
    async getInfo() {
      const { data } = await this.$api.getMy(this.address)
      if (data.status === 1) {
        this.info = data.data
        this.isout = data.data.isout
        this.max_invest = new BigNumber(data.data.max_invest).toNumber()
        this.min_invest = new BigNumber(data.data.min_invest).toNumber()
        this.investNumber = this.min_invest
        console.log(this.max_invest, this.min_invest)
        this.isCover = Number(data.data.cover_claimable) !== 0
      } else {
        Toast.fail(data.msg)
      }
    }
  },
  created() {
    // 总线注入
    this.address = window.address
    this.bus.$on('addressChange', (address) => {
      this.address = address
    })
  }
}
</script>
<style lang="less" scoped>
.my{
  font-size: .16rem;
  padding: .1rem 4% .5rem 4%;
  box-sizing: border-box;
  overflow-y: scroll;
  .header {
    height: 0.25rem;
    padding: 0rem 0.03rem;
    color: #FFFFFF;
  }
}
.contract {
    padding: 0.3rem 0.1rem 0.15rem;
    height: 1.3rem;
    box-sizing: border-box;
    position: relative;
    width: 100%;
