<script>
import BackItem from '../components/BackItem'
import CurrentBox from '../components/CurrentBox'
import BigNumber from 'bignumber.js'
// import VeLine from 'v-charts/lib/line.common'
import { Toast } from 'vant'
import dayjs from 'dayjs'
import helper from '../utils/helper'
export default {
  components: {
    BackItem,
    CurrentBox
    // VeLine
  },
  name: 'Home',
  data() {
    this.extend = {
      'xAxis.0.axisLabel.color': '#fff',
      'yAxis.0.axisLabel.color': '#fff'
    }
    return {
      icon: require('../assets/icon.png'),
      status: false,
      info: {},
      coverHistory: [],
      paidStatus: false,
      // 是否赔付
      isCover: false,
      chartData: {
        columns: ['date', 'amount'],
        rows: []
      },
      investStatus: false,
      isout: true,
      myInfo: {},
      max_invest: 0,
      min_invest: 0,
      investNumber: '0.1',
      address: '',
      bg: require('../assets/head.png'),
      coin: require('../assets/coin.png'),
      comption: require('../assets/comption.png'),
      defend: require('../assets/defend.png'),
      eth: require('../assets/eth.png')
    }
  },
  watch: {
    address(newValue) {
      if (newValue) {
        this.getIndex()
        this.getInfo()
      }
    }
  },
  methods: {
    background(type) {
      return {
        background: `url(${this[type]})`,
        backgroundSize: '100% 100%'
      }
    },
    async handlePaider() {
      try {
        const { data } = await this.$api.claimInfo({ address: this.address, contract: 'cover' })
        if (data.status === 1) {
          const params = data.data
          const reuslt = await helper.reported(params.amount, params.hash, params.sign)
          if (reuslt) {
            Toast.success(this.$t('领取成功'))
          }
        } else {
          Toast.fail(data.msg)
        }
      } catch (error) {
        console.log(this.$t('领取赔付失败') + error)
        Toast.fail(this.$t('领取赔付失败'))
      }
    },
    handleAction() {
      this.handleInvest()
    },
    async handleInvest() {
      try {
        const a = Number(this.investNumber)
        // 参与投资是个整数
        if (!(a >= this.min_invest && a <= this.max_invest)) {
          Toast.fail(this.$t('请在规定范围内投资'))
          return
        }
        if (a !== this.min_invest && new BigNumber(this.investNumber).mod(0.1).toNumber() !== 0) {
          Toast.fail(this.$t('需要0.1的整数倍'))
          return
        }
        console.log(this.investNumber)
        this.investStatus = false
        const result = await helper.invest(this.investNumber)
        console.log(result)
        if (result) {
          Toast.success(this.$t('投资成功'))
          await this.getInfo()
        }
      } catch (error) {
        console.log(this.$t('投资失败') + error)
        Toast.fail(this.$t('投资失败'))
      }
    },
    // 我的页面基本信息
    async getInfo() {
      const { data } = await this.$api.getMy(this.address)
      if (data.status === 1) {
        this.myInfo = data.data
        this.isout = data.data.isout
        this.max_invest = new BigNumber(data.data.max_invest).toNumber()
        this.min_invest = new BigNumber(data.data.min_invest).toNumber()
        // this.isout = true
        // this.max_invest = 30
        // this.min_invest = 0
        this.investNumber = this.min_invest
        console.log(this.max_invest, this.min_invest)
        this.isCover = Number(data.data.cover_claimable) !== 0
      } else {
        Toast.fail(data.msg)
      }
    },
    async handlePaid() {
      const { data } = await this.$api.getCoverHistory(this.address)
      if (data.status === 1) {
        this.coverHistory = data.data
        this.paidStatus = true
      } else {
        Toast.fail(data.msg)
      }
    },
    handleSearch(contract) {
      window.open(`https://cn.etherscan.com/address/${contract}`)
    },
    async getIndex() {
      const { data } = await this.$api.getIndex()
      if (data.status === 1) {
        this.info = data.data
        this.setEchart()
      } else {
        Toast.fail(data.msg)
      }
    },
    setEchart() {
      const echartData = this.info.users_by_date.map(value => {
        value['date'] = dayjs(value.time * 1000).format('MM-DD:hh:mm')
        return value
      })
      this.chartData.rows = echartData.reverse()
    }
  },
  created() {
    // 总线注入
    this.address = window.address
    this.bus.$on('addressChange', (address) => {
      this.address = address
    })
  }
}
</script>
