<template>
    <div class="warm fade" v-if="inner_status">
    <!-- 是否启用了拥有metamask -->
    <div  class="warm flex_col">
      <img :src="warmIcon" class="warm-icon" alt="">
      <span class="warm-title color-f6a mg-t-10">请使用metaMask访问此页面</span>
    </div>
    </div>
</template>
<script>
export default {
  props: {
    value: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      warmIcon: require('../assets/warm.png'),
      inner_status: this.value
    }
  },
  watch: {
    value: {
      handler(newValue) {
        this.inner_status = newValue
      }
    }
  }
}
</script>
