<template>
 <transition name="minerBox">
   <div  v-if="inner_status">
    <div class="fade">
      <div class="succ" :style="succStyle()">
          <slot name="custom">
            <p class="succ_title">{{title}}</p>
            <slot name="content-custom">
               <p class="succ_content">{{content}}</p>
            </slot>
            <div class="flex_row_around" style="margin-top:.2rem">
              <div class="current-btn" v-if="comfirmBtnStatus" @click="handleComfirm">{{comfirmBtnText}}</div>
              <div class="cancel-btn" v-if="cancelBtnStatus" @click="handleCancel">{{real_cancelBtnText}}</div>
            </div>
          </slot>
      </div>
    </div>
   </div>
 </transition>
</template>
<script>
export default {
  props: {
    title: {
      type: String,
      default: '确认弹窗'
    },
    content: {
      type: String,
      default: '弹窗内容'
    },
    value: {
      type: Boolean,
      default: true
    },
    comfirmBtnText: {
      type: String,
      default: '确认'
    },
    comfirmBtnStatus: {
      type: Boolean,
      default: true
    },
    width: {
      type: Number,
      default: 40
    },
    cancelBtnText: {
      type: String,
      default: '取消'
    },
    cancelBtnStatus: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      inner_status: this.value,
      real_cancelBtnText: this.cancelBtnText
    }
  },
  watch: {
    value (newValue, oldValue) {
      this.inner_status = newValue
    }
  },
  methods: {
    succStyle () {
      return { width: `${this.width}vw` }
    },
    handleComfirm () {
      this.$emit('on-comfirm')
    },
    handleCancel () {
      this.$emit('on-cancel')
    }
  },
  created() {
    this.real_cancelBtnText = localStorage.getItem('language') === 'zh' ? '取消' : localStorage.getItem('language') === 'gb' ? 'cancel' : localStorage.getItem('language') === 'jp' ? 'キャンセル' : '취소'
  }
}
</script>
