    // position: absolute;
    // top: 30%;
    padding: .2rem;
    border-color: #F6AF4D;
    box-sizing: border-box;
    text-align: left;
}
.succ_title{
    font-size: .18rem;
    color:#000000;
    font-weight: bolder;
}
.succ_content{
    font-size: .15rem;
    color:#000000;
